import React, { useEffect } from 'react';

import { isKorean } from '@/util';
import { weeklyPickUrl } from '@/constant';

const WeeklyPickRedirection: React.FC = () => {
  useEffect(() => {
    window.location.href = isKorean ? weeklyPickUrl.KO : weeklyPickUrl.EN;
  });

  return <div />;
};

export default WeeklyPickRedirection;
